.component {
  background-color: #ffffff;
  height: 72px;
  position: relative;
  width: 1440px;
}

.component .u-dash {
  color: transparent;
  font-family: "League Spartan", Helvetica;
  font-size: 48px;
  font-weight: 400;
  left: 23px;
  letter-spacing: 0;
  line-height: normal;
  position: absolute;
  top: -40px;
  white-space: nowrap;
  width: 140px;
}

.component .text-wrapper {
  color: #004aad;
}

.component .span {
  color: #000000;
}

.component .other-links {
  align-items: center;
